/* src/App.css */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #493e4e; /* Add a background color */
}

.App {
  text-align: center;
}

h1 {
  margin-bottom: 20px; /* Add margin to create space below the heading */
  color: #dedede; /* Set color for the heading */
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: #aea5a5; */
}

.board {
  border: 1px solid #333;
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-gap: 2px;
  margin-top: 20px;
}

.cell {
  font-size: 2em;
  height: 100px;
  width: 100px;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
}

.cell:hover {
  background-color: #dedede; /* Set the desired background color on hover */
}

.game-info {
  margin-top: 20px;
  color: #dedede;
}

